define('mapStops',["map", "stopCache", "eventServices/mapCenterChanged", "eventServices/mapStopClicked", "eventServices/stopAdded", "utils/distance"], 
function(map, stopCache, mapCenterChanged, mapStopClicked, stopAdded, distance) {

    function searchForStops() {
        stopCache.addStopsAround(map.getCenter().lat(), map.getCenter().lng());
    }

    var image = {
        url: 'images/bus_marker_wide.png',
        size: new google.maps.Size(74, 37),
        origin: new google.maps.Point(0, 0),
        anchor: new google.maps.Point(15, 41)
    };

    function createStopMarker(stop) {
        var stopMarker = 
        new MarkerWithLabel({
           position: new google.maps.LatLng(stop.location.latitude, stop.location.longitude),
           map: map.googleMap,
           labelContent: stop.code,
           labelClass: "labels", // the CSS class for the label
           labelAnchor: new google.maps.Point(-10, 32),
           title: stop.name,
           icon: image
        })
        // new google.maps.Marker({
        //     map: map.googleMap,
        //     title: stop.name,
        //     icon: image,
        //     position: new google.maps.LatLng(stop.location.latitude, stop.location.longitude)
        // });

        google.maps.event.addListener(stopMarker, 'click', function() {
            mapStopClicked.broadcast(stop);
        });
    }

    mapCenterChanged.listen(function() {
        searchForStops();
    });

    stopAdded.listen(createStopMarker);

    searchForStops();
});
